@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400&display=swap&subset=latin-ext);
:root {
  font-size: 10px;
}
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(44, 43, 53);
  font-weight: 200;
  height: 100vh;
  width: 100%;
}
* {
  font-weight: 200;
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
}

.main-container {
  color: rgb(188, 191, 204);
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main__tittle {
  font-size: 4.8rem;
  margin: 0;
  text-align: center;
}
.sub__tittle {
  margin: 0;
}
@media (max-width: 600px) {
  :root {
    font-size: 8px;
  }
  p.sub__tittle {
    font-size: 2rem;
  }
}

.country_container {
  flex-basis: 100%;
  margin: 20px 0;
  padding: 10px 20px;
  text-align: center;
  color: rgb(188, 191, 204);
  font-size: 2.4rem;
}
.stats {
  margin: 0;
  padding: 0;
  font-size: 2.6rem;
  color: rgb(245, 83, 83);
}
.row {
  display: flex;
  justify-content: center;
}
.item {
  margin: 0 10px;
}
.item h2 {
  margin: 0;
}
.row-column {
  flex-direction: column;
}
.stats,
.item,
.tittle {
  text-transform: uppercase;
}
.item h2 {
  font-size: 1.8rem;
}

